"use client";

import Image from 'next/image';
import Link from 'next/link';
import { Users, BookOpen, Sparkles, ArrowRight } from 'lucide-react';

import { Button } from '@/components/ui/button';
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from '@/components/ui/card';
import { useState } from 'react';

const features = [
  {
    icon: <Users className="h-8 w-8 text-primary" />,
    title: 'Plant Swap Community',
    description: 'Connect with local plant lovers to trade plants and cuttings. Grow your collection and make new friends.',
    href: '/plant-swap',
    cta: 'Browse Listings',
  },
  {
    icon: <BookOpen className="h-8 w-8 text-primary" />,
    title: 'Interactive Care Guides',
    description: 'Whether you\'re a beginner or a seasoned pro, our step-by-step guides will help your green companions thrive.',
    href: '/guides',
    cta: 'Learn More',
  },
  {
    icon: <Sparkles className="h-8 w-8 text-primary" />,
    title: 'AI Plant Assistant',
    description: 'Unsure about a plant? Upload a photo and get instant, AI-powered care tips and identification.',
    href: '/assistant',
    cta: 'Try The AI',
  },
  // 新增 Events
  {
    icon: <span className="inline-block h-8 w-8 bg-primary/10 rounded-full flex items-center justify-center"><span className="material-icons text-primary">event</span></span>,
    title: 'Events',
    description: 'Discover and join upcoming plant events, meetups, and workshops near you.',
    href: '/events',
    cta: 'View Events',
  },
  // 新增 Map
  {
    icon: <span className="inline-block h-8 w-8 bg-primary/10 rounded-full flex items-center justify-center"><span className="material-icons text-primary">map</span></span>,
    title: 'Map',
    description: 'Explore plant events and shops on the interactive map (coming soon).',
    href: '/map',
    cta: 'Open Map',
  },
];

export default function Home() {
  return (
    <div className="flex flex-col min-h-[100dvh]">
      <section className="w-full py-20 md:py-32 lg:py-40 flex flex-col items-center justify-center relative overflow-hidden">
        <div className="container max-w-screen-xl mx-auto px-4 md:px-8 relative z-10">
          <div className="grid gap-6 lg:grid-cols-2 lg:gap-12 xl:gap-24 items-center">
            <div className="flex flex-col justify-center space-y-4">
              <div className="space-y-2">
                <h1 className="text-4xl font-bold tracking-tighter sm:text-5xl xl:text-6xl/none font-headline hero-shadow text-primary">
                  Welcome to <span className="text-primary">GreensWrld</span>
                </h1>
                <p className="max-w-[600px] md:text-2xl hero-shadow font-extrabold text-black mt-4 mb-2 text-center" style={{ fontFamily: 'Helvetica Neue, Arial, sans-serif', letterSpacing: '-0.01em' }}>
                  Grow Your World
                </p>
              </div>
              <div className="flex flex-col gap-2 min-[400px]:flex-row">
                <Button asChild size="lg" className="bg-primary hover:bg-primary/90 text-primary-foreground">
                  <Link href="/plant-swap">
                    Start Swapping
                    <ArrowRight className="ml-2 h-4 w-4" />
                  </Link>
                </Button>
                <Button asChild size="lg" variant="outline" className="text-accent border-accent hover:bg-accent/10 hover:text-accent">
                  <Link href="/assistant">
                    Ask Gaia
                  </Link>
                </Button>
              </div>
            </div>
            <div className="flex justify-center items-center">
              <Image
                src="/logo.png"
                width={960}
                height={960}
                alt="GreensWrld Trademark Logo"
                className="rotate-logo"
                style={{ maxWidth: '100%', height: 'auto' }}
                unoptimized
              />
            </div>
          </div>
        </div>
      </section>

      {/* Our Story Section */}
      <section className="w-full py-24 md:py-32 flex justify-center items-center" style={{ background: 'rgba(224, 247, 250, 0.80)', backdropFilter: 'blur(8px)' }}>
        <div className="w-full max-w-3xl mx-auto px-4 md:px-8">
          <div className="rounded-3xl shadow-xl bg-white/70 p-10 md:p-16 flex flex-col items-center gap-8 border border-[#e0f7fa]/60">
            <h2 className="text-3xl md:text-4xl font-bold text-primary text-center mb-2" style={{ fontFamily: 'Helvetica Neue, Arial, sans-serif' }}>Our Story</h2>
            <OurStoryExpandable />
          </div>
        </div>
      </section>

      <section className="w-full py-12 md:py-24 lg:py-32">
        <div className="container max-w-screen-xl mx-auto px-4 md:px-8">
          <div className="flex flex-col items-center justify-center space-y-4 text-center mb-12">
            <div className="inline-block rounded-lg bg-muted px-3 py-1 text-sm">Key Features</div>
            <h2 className="text-3xl font-bold tracking-tighter sm:text-5xl font-headline hero-shadow text-black" style={{ fontFamily: 'Helvetica Neue, Arial, sans-serif' }}>
              Everything for the Modern Plant Parent
            </h2>
            <div className="max-w-[900px] mx-auto flex flex-col items-center justify-center hero-shadow font-bold text-black md:text-xl/relaxed lg:text-base/relaxed xl:text-xl/relaxed" style={{ fontFamily: 'Helvetica Neue, Arial, sans-serif' }}>
              <div className="text-center">From finding your next plant to ensuring it flourishes,</div>
              <div className="text-center">GreensWrld provides all the tools you need.</div>
            </div>
          </div>
          <div className="mx-auto grid items-start gap-8 sm:max-w-4xl sm:grid-cols-2 md:gap-12 lg:max-w-5xl lg:grid-cols-3">
            {features.map((feature) => (
              <Card key={feature.title} className="shadow-md hover:shadow-xl transition-shadow flex flex-col">
                <CardHeader className="flex flex-col items-center text-center gap-4 p-6">
                  <div className="rounded-full bg-primary/10 p-4">
                    {feature.icon}
                  </div>
                  <CardTitle className="text-xl font-bold font-headline">{feature.title}</CardTitle>
                </CardHeader>
                <CardContent className="p-6 pt-0 text-center flex-grow">
                  <CardDescription>{feature.description}</CardDescription>
                </CardContent>
                <div className="p-6 pt-0">
                  <Button asChild className="w-full bg-accent text-accent-foreground hover:bg-accent/90">
                    <Link href={feature.href}>{feature.cta}</Link>
                  </Button>
                </div>
              </Card>
            ))}
          </div>
        </div>
      </section>
    </div>
  );
}

function OurStoryExpandable() {
  const [expanded, setExpanded] = useState(false);
  return (
    <div className="flex flex-col items-center w-full">
      <p className="text-lg md:text-xl text-black font-medium leading-relaxed mb-8 text-center" style={{ fontFamily: 'Helvetica Neue, Arial, sans-serif' }}>
        Every great change starts with a single idea, much like a mighty oak begins with a tiny acorn. At GreensWrld, our story began with a simple, yet profound, realization: the world needed a better way to connect with the green heartbeat of our planet. We saw fragmented knowledge, isolated growers, and missed opportunities for collective growth.
      </p>
      {!expanded && (
        <button
          className="inline-block px-8 py-3 rounded-full bg-primary text-primary-foreground font-bold shadow-lg hover:bg-primary/90 transition mb-4 text-lg"
          onClick={() => setExpanded(true)}
        >
          Click to read more
        </button>
      )}
      {expanded && (
        <div className="text-lg md:text-xl text-black font-medium leading-relaxed text-center space-y-6" style={{ fontFamily: 'Helvetica Neue, Arial, sans-serif' }}>
          <p>
            We envisioned a global canvas for growth, a place where passion for plants could blossom into tangible action. What if we could build a platform that was more than just an app? What if it was a community hub, a one-stop shop for every gardening necessity, every farming innovation, and every shared dream of a greener future? We imagined something akin to GitHub for agriculture, where ideas are cultivated, projects are shared, and collective wisdom truly flourishes.
          </p>
          <p>
            From this vision, GreensWrld was born. We set out to create a mobile-friendly, location-based platform that seamlessly connects you with local plant services, essential tools, and a vibrant network of fellow enthusiasts. Whether you're looking for a farmer's market, need tips for your home garden, or want to share your latest harvest, GreensWrld is designed to empower you.
          </p>
          <p>
            We believe that by nurturing change one seed at a time, through education, community engagement, and accessible resources, we can cultivate a world where sustainable living isn't just a concept, but a shared reality. Join us in growing a greener, more connected world.
          </p>
        </div>
      )}
    </div>
  );
}
